
html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
table, caption, tbody, tfoot, thead, tr, th, td {
  margin: 0;
  padding: 0;
  border: 0;
}

.node {
  width: 25px;
  height: 25px;
  outline: 1px solid black;
  display: inline-block;
  margin: 2px;
  padding: 0px; 
}


